<template>
  <div id="mem">
    <!-- 会员信息 -->
    <div id="members">
      <div class="box1">
        <div class="box1_left">
          <img
            :src="headUrl"
            style="width:30px;height:30px;margin-right:10px;border-radius: 20px;"
            alt
          />
          <div>
            <p>
              <span class="c_00">{{ nickName }}</span>
              <span class="c_f60">{{ levelName }}</span>
            </p>
            <p>
              <span>积分</span>
              <span>{{ integral }}</span>
            </p>
          </div>
        </div>
        <div @click="showEdit = true">修改></div>
        <van-popup
          v-model="showEdit"
          class="couponBox"
        >
          <div>
            <h2>修改会员详情</h2>
            <div>
              <van-field
                v-model="input_consumeNum"
                type="number"
                label="入住间夜(天)"
                placeholder="请输入入住间夜"
              />
              <van-field
                v-model="input_consumeAmount"
                type="number"
                label="消费金额(元)"
                placeholder="请输入消费金额"
              />
            </div>
            <mt-button
              class="button"
              @click="editMemberInfo"
            >保存</mt-button>
          </div>
        </van-popup>
      </div>
      <div>
        <div class="mem_display">
          <p class="w50">
            <span>真实姓名:</span>
            <span class="c_00">{{ realName }}</span>
          </p>
          <p class="w59">
            <span>身份证号:</span>
            <span class="c_00">{{ idenIds }}</span>
          </p>
        </div>
        <div class="mem_display">
          <p class="w50">
            <span>手机号 :</span>
            <span class="c_00">{{ phone }}</span>
          </p>
          <p class="w59">
            <span>办理时间:</span>
            <span class="c_00">{{ createTime }}</span>
          </p>
        </div>
        <div class="mem_display">
          <p class="w50">
            <span>消费金额(元):</span>
            <span>{{ consumeAmount }}</span>
          </p>
          <p class="w59">
            <span>入住间夜(天):</span>
            <span>{{ consumeNum }}</span>
          </p>
        </div>
        <div class="mem_display">
          <p class="w50">
            <span>最近入住:</span>
            <span>{{ lastRoomTime?lastRoomTime:'' }}</span>
          </p>
          <!-- <p class="w59">
            <span>平均消费(元):</span>
            <span>{{ consumeAmount/consumeNum?(consumeAmount/consumeNum).toFixed(2) :'0'}}</span>
          </p>-->
        </div>
      </div>
    </div>
    <!-- 优惠券 -->
    <div id="coupon">
      <div class="coupon">
        <p class="weight">优惠券</p>
        <p
          class="botton"
          @click="sendCouponShow"
        >发券</p>
      </div>
      <div class="couponList">
        <div
          v-for="(item,i) in coupons"
          :key="i"
          class="times"
        >
          <div style="width:20%;">
            <span
              class="c_f60 f12"
              :style="{'color': (item.endTime < today || item.useDefault != 0? '#fab6b6':'#f04d4e')}"
            >&yen;{{ item.couponAmount }}</span>
            <br />
            <span
              v-if="item.endTime < today"
              :style="{'color': (item.endTime < today || item.useDefault != 0? '#b6b6b6':'#f04d4e')}"
            >已过期</span>
            <span v-else>{{ item.useDefault == 0?"未使用":'已使用' }}</span>
          </div>
          <div>
            <span
              class="f11"
              :style="{'color': (item.endTime < today || item.useDefault != 0? '#b6b6b6':'#2c3e50')}"
            >
              满{{ item.fullAmount }}可用&emsp;
              <span
                class="scale"
                :style="{'color': (item.endTime < today || item.useDefault != 0? '#b6b6b6':'#2c3e50')}"
              >{{ item.text }}可用</span>
            </span>
            <br />
            <span :style="{'color': (item.endTime < today || item.useDefault != 0? '#b6b6b6':'#2c3e50')}">{{ item.startTime }} 至 {{ item.endTime }}</span>
          </div>
        </div>
      </div>
      <van-popup
        v-model="showCoupon"
        class="couponBox"
      >
        <div>
          <h2>发送优惠券</h2>
          <div>
            <van-field
              v-model="memName"
              label="发送面额"
              type="number"
              placeholder="请输入面额"
            />
            <van-field
              v-model="number"
              label="发送数量"
              type="number"
              placeholder="请输入数量"
            />
            <van-field
              v-model="price"
              label="使用门槛"
              type="number"
              placeholder="满多少金额可使用"
            />
            <van-cell
              title="有效期"
              placeholder="选择有效时间段"
              :value="date"
              @click="showTime = true , showCoupon = false"
            />
            <div id="range">
              <div class="receive">
                <label>使用范围</label>
                <ul v-if="ordertTypeAll.length">
                  <li
                    v-for="(item,index) in ordertTypeAll"
                    :key="index"
                  >
                    <van-checkbox
                      :value="item.dictLabel"
                      v-model="item.isChecked"
                      checked-color="#ff7d41"
                      @click="chooseChange(item.dictValue)"
                    >{{item.dictLabel}}</van-checkbox>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <mt-button
            class="button"
            @click.native="sendCoupon"
          >确定</mt-button>
        </div>
      </van-popup>
      <van-calendar
        v-model="showTime"
        type="range"
        @confirm="onConfirm"
      />
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { GetBusinessUserCouponByuser, PostBusinessUserCouponAdd, PostBusinessHotelMemberEdit } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
import cookie from '@/common/cookie.js';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            realName: '',
            phone: '',
            consumeAmount: '',
            createTime: '',
            integral: '',
            headUrl: '',
            nickName: '',
            realName: '',
            consumeNum: '',
            lastRoomTime: '',
            idenIds: '',
            coupons: [],
            records: [],
            sort: '',
            showEdit: false,
            IdCord: '',
            showCoupon: false,
            memName: '',
            number: '',
            price: '',
            date: '选择有效时间段',
            showTime: false,
            ordertTypeAll: [],
            ordertTypeShow: [],
            isChecked: false,
            selectedData: [],
            today: '',
            scaleName: '',
            roomSn: '',
            diffDay: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '会员详情');
        let memberDetail = JSON.parse(localStorage.getItem('memberDetail_info'));
        this.hotelId = cookie.getCookie('currentID');
        this.realName = memberDetail.sysUser.realName;
        this.phone = memberDetail.sysUser.phone;
        this.consumeAmount = memberDetail.consumeAmount;
        this.input_consumeAmount = memberDetail.consumeAmount;
        this.createTime = memberDetail.createTime;
        this.integral = memberDetail.integral;
        this.userId = memberDetail.userId;
        this.id = memberDetail.id;
        this.headUrl = memberDetail.sysUser.headUrl;
        this.nickName = memberDetail.sysUser.nickName;
        this.idenIds = memberDetail.sysUser.idenIds;
        this.consumeNum = memberDetail.consumeNum ? memberDetail.consumeNum : '0';
        this.input_consumeNum = this.consumeNum;
        this.lastRoomTime = memberDetail.lastRoomTime;
        this.levelName = memberDetail.hotelMemberLevel.memberLevelTemplate.levelName;
        this.getUserCoupon();
    },
    methods: {
        // 根据用户查询酒店优惠券
        getUserCoupon() {
            let params = {
                userId: this.userId,
                hotelId: this.hotelId
            };
            GetBusinessUserCouponByuser(params).done((res) => {
                this.getOrderType();
                this.coupons = res.data;
                this.coupons.sort(function(a, b) {
                    return a.couponAmount < b.couponAmount ? 1 : -1;
                });
                this.coupons.sort(function(a, b) {
                    return a.endTime < b.endTime ? 1 : -1;
                });
            });
            const days = dayjs();
            this.today = dayjs().format('YYYY-MM-DD');
        },
        // 使用范围-ordertype
        getOrderType() {
            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                this.ordertTypeAll = res.data;
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                for (let i = 0; i < this.coupons.length; i++) {
                    let orderTypeArr = this.coupons[i].orderType.split(',');
                    for (let j = 0; j < orderTypeArr.length; j++) {
                        if (this.coupons[i].text) {
                            this.coupons[i].text += '、' + orderSourceMap[orderTypeArr[j]];
                        } else {
                            this.coupons[i].text = orderSourceMap[orderTypeArr[j]];
                        }
                    }
                }
            });
        },
        // 根据用户查询酒店优惠券
        getMemberRecord() {
            let params = {
                userId: this.userId,
                hotelId: this.hotelId
            };
            GetBusinessUserCouponByuser(params).done((res) => {
                this.records = res.data;
            });
            const days = dayjs();
            this.today = dayjs().format('YYYY-MM-DD');
        },
        formatDate(date) {
            let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
            this.doble.push(doble);
            this.startTime = this.doble[0];
            this.endTime = this.doble[1];
            return doble;
        },
        onConfirm(date) {
            const [start, end] = date;
            this.showTime = false;
            this.showCoupon = true;
            this.doble = [];
            this.date = `${this.formatDate(start)} 至 ${this.formatDate(end)}`;
        },
        // 发券
        sendCouponShow() {
            this.showCoupon = true;
            this.sendOrderType();
        },
        // 使用范围-ordertype
        sendOrderType() {
            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                this.ordertTypeAll = res.data;
            });
        },
        // 单选的change事件
        chooseChange(dictValue) {
            if (this.selectedData.indexOf(dictValue) > -1) {
                this.removeByValue(this.selectedData, dictValue);
            } else {
                this.selectedData.push(dictValue);
            }
            if (this.selectedData.length < this.ordertTypeAll.total) {
                this.AllChecked = true;
            } else {
                this.AllChecked = false;
            }
            this.orderType = this.selectedData.toString();
        },
        // 过滤
        removeByValue(arr, val) {
            for (var i = 0; i < arr.length; i++) {
                if (arr[i] == val) {
                    arr.splice(i, 1);
                    break;
                }
            }
        },
        // 全选和反选
        checkAll() {
            let list = this.ordertTypeAll;
            if (this.AllChecked) {
                list.forEach((element) => {
                    element.isChecked = true;
                });
                this.selectedData = [];
            } else {
                list.forEach((element) => {
                    element.isChecked = false;
                    if (this.selectedData.indexOf(element.name) < 0) {
                        this.selectedData.push(element.name);
                    }
                });
            }
        },
        // 发送优惠券
        sendCoupon() {
            if (!this.memName) {
                Toast({
                    message: '请输入面额',
                    duration: 900
                });
                return false;
            }
            if (!this.number) {
                Toast({
                    message: '请输入数量',
                    duration: 900
                });
                return false;
            }
            if (!this.price) {
                Toast({
                    message: '请输入满减金额',
                    duration: 900
                });
                return false;
            }
            if (!this.startTime) {
                Toast({
                    message: '请输选择有效时间段',
                    duration: 900
                });
                return false;
            }
            if (!this.orderType) {
                Toast({
                    message: '请输选择领取范围',
                    duration: 900
                });
                return false;
            }
            let data = {
                couponSource: '2',
                userId: this.userId,
                hotelId: this.hotelId,
                couponAmount: this.memName,
                fullAmount: this.price,
                couponNum: this.number,
                startTime: this.startTime,
                endTime: this.endTime,
                orderType: this.orderType
            };
            PostBusinessUserCouponAdd(data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.getUserCoupon();
            });
            this.showCoupon = false;
        },
        // 修改会员信息
        editMemberInfo() {
            if (!this.input_consumeNum) {
                Toast({
                    message: '请输入入住间夜',
                    duration: 900
                });
                return false;
            }
            if (!this.input_consumeAmount) {
                Toast({
                    message: '请输入消费金额',
                    duration: 900
                });
                return false;
            }
            let params = {
                id: this.id,
                consumeNum: this.input_consumeNum,
                consumeAmount: this.input_consumeAmount
            };
            PostBusinessHotelMemberEdit(params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                let memberD = JSON.parse(localStorage.getItem('memberDetail_info'));
                this.consumeAmount = memberD.consumeAmount = params.consumeAmount;
                this.consumeNum = memberD.consumeNum = params.consumeNum;
                localStorage.setItem('memberDetail_info', JSON.stringify(memberD));
                this.showEdit = false;
            });
        }
    }
};
</script>
<style scoped>
.c_00 {
    color: #000;
}
.c_f60 {
    color: #f60;
}
.f12 {
    font-size: 1.1rem;
}
.f11 {
    font-size: 0.8rem;
    font-weight: bold;
}
.a {
    color: red;
}
.b {
    color: green;
}
.scale {
    font-size: 0.6rem;
    font-weight: normal;
}
.mem_display {
    display: flex;
    padding: 6px 10px;
}
.mem_display > p {
    text-align: left;
}
.w50 {
    width: 45%;
    white-space: nowrap;
}
.w59 {
    white-space: nowrap;
    width: 50%;
}
#mem {
    position: fixed;
    top: 50px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
    font-size: 0.6rem;
}
#members {
    color: #a6a6a6;
    background: #fff;
    margin-top: 10px;
}
#members > .box1 {
    display: flex;
    justify-content: space-between;
    padding: 6px 10px;
    align-items: center;
}
#members > .box1 > .box1_left {
    display: flex;
    text-align: left;
}
#coupon {
    background: #fff;
    padding: 6px 10px 0 10px;
    margin-top: 20px;
}
.weight {
    font-weight: bold;
}
#coupon > .coupon {
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f0f0f0;
    padding: 8px 0;
}
#coupon > .coupon > .botton {
    font-size: 0.7rem;
    width: 16%;
    background: #ff7d41;
    border-radius: 10px;
    color: #fff;
    line-height: 22px;
}
#coupon > .couponList > .times {
    display: flex;
    align-items: center;
    line-height: 20px;
    text-align: left;
    border-bottom: 1px solid #f0f0f0;
    padding: 6px 0;
}
.s_between {
    justify-content: space-between;
}
/* 发送优惠券 */
.couponBox {
    width: 90%;
    border-radius: 5px;
}
h2 {
    text-align: left;
    padding-left: 10%;
    margin: 20px 0;
}
.couponBox > div > div {
    padding: 0 5%;
}
.couponBox > div > button {
    background: #ff7d41;
    color: #fff;
    width: 100%;
    border-radius: 0;
    font-size: 0.8rem;
    margin-top: 10px;
}
#range > .receive {
    display: flex;
    padding: 8px 16px;
    text-align: left;
    color: #000;
}
#range > .receive > label {
    width: 90px;
}
#range > .receive > ul {
    width: 180px;
}
#range > .receive > ul > li {
    height: 30px;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell__value {
    min-width: 66%;
    text-align: left;
}
</style>



